<template>
  <LayoutPage class="cocktails-viewport">
    <div class="viewport">
      <div class="viewport-main">

        <div class="logo">
          <img src="./img/champagne.svg" alt="">
        </div>

        <div class="viewport-input">
          <div class="form-group">
            <input type="number" id="ml" v-model="ml" placeholder="ml">
            <label for="ml">ml</label>
          </div>

          <div class="form-group">
            <input type="number" id="percentage" v-model="percentage" placeholder="酒精度數">
            <label for="percentage">%</label>
          </div>
        </div>

        <div class="message">
          <p>攝取酒精 - <span>{{ alcohol }}</span> / g</p>
          <p>約 <span>{{ replacement }}</span> 小時後代謝完</p>
        </div>

        <button class="c-btn c-btn:clear" @click="clear()">
          <div class="c-btn__name">CLEAR</div>
        </button>
      </div>
    </div>
  </LayoutPage>
</template>

<script setup>
// import Markdown from 'vue3-markdown-it';
import { defineOptions, ref, computed } from 'vue'
import LayoutPage from '@/components/layout/page'

defineOptions({ name: 'Cocktails' })

const ml = ref(null)
const percentage = ref(null)

const clear = () => {
  ml.value = null
  percentage.value = null
}

// 計算公式
const alcohol = computed(() => {
  const num = ml.value * percentage.value / 100 * .8
  // 喝下的c.c數或ml數 × (酒精度數 / 100) × 0.8(酒精比重) = 酒精攝取量(酒精重量)

  return num.toFixed(2)
})

const replacement = computed(() => {
  const num = alcohol.value / 10
  // 人體每小時大約只能分解一個酒精單位（10 公克）

  return num.toFixed(0)
})

</script>

<style lang="sass" scoped>
@import @sass/init
@import @sass/helpers

@import ./style
</style>
